.grid {
  box-sizing: border-box;
  list-style: none;
  display: block;
  margin: 0;
  padding: 0;
  min-width: 100%;
  border: 0 solid #000;
  display: flex !important;
  align-content: stretch;
}

.grid__wrapper {
  overflow: hidden;
}

.grid__cell {
  box-sizing: border-box;
  min-width: 0%;
  max-width: 100%;
  display: block;
  padding: 0;
  margin: 0;
  background-clip: padding-box !important;
  border: 0 solid transparent;
  flex-grow: 1;
  flex-shrink: 1;
  outline-color: #000;
  border-color: #000;
}

.grid__cell--vAlign {
  align-self: center;
}

.grid__cell--align-left {
  justify-content: flex-start;
}

.grid__cell--align-center {
  justify-content: center;
}

.grid__cell--align-right {
  justify-content: flex-end;
}

.grid__cell.grid {
  display: flex;
}

.grid__cell, .grid__cell * > {
  max-width: 100%;
  flex-shrink: 1;
}

.grid__cell--noGrowth {
  flex-grow: 0;
}

.grid__cell--noShrink {
  flex-shrink: 0;
}

.grid__cell--noResize {
  flex-shrink: 0;
  flex-grow: 0;
}

.grid__inner {
  display: block;
}

